// Small devices
@mixin sm {
    @media (max-width: #{$screen-sm-max}) {
        @content;
    }
}
 
 // Medium devices
 @mixin md {
    @media (max-width: #{$screen-md-max}) {
        @content;
    }
 }
 
 // Large devices
 @mixin lg {
    @media (max-width: #{$screen-lg-max}) {
        @content;
    }
}
 
 // Extra large devices
 @mixin xs {
    @media (max-width: #{$screen-xs-max}) {
        @content;
    }
}

@mixin header_break_point {
    @media (max-width: 1010px) {
        @content;
    }
}